import axios, { isAxiosError } from '@nextcloud/axios';
import { showError, showSuccess } from '@nextcloud/dialogs';
import { emit } from '@nextcloud/event-bus';
import { FileType, NodeStatus } from '@nextcloud/files';
import { translate as t } from '@nextcloud/l10n';
import { dirname } from '@nextcloud/paths';
import { defineComponent, inject } from 'vue';
import NcTextField from '@nextcloud/vue/dist/Components/NcTextField.js';
import { useNavigation } from "../../composables/useNavigation";
import { useRouteParameters } from "../../composables/useRouteParameters.ts";
import { useRenamingStore } from "../../store/renaming.ts";
import { getFilenameValidity } from "../../utils/filenameValidity.ts";
import logger from "../../logger.ts";
export default defineComponent({
  name: 'FileEntryName',
  components: {
    NcTextField
  },
  props: {
    /**
     * The filename without extension
     */
    basename: {
      type: String,
      required: true
    },
    /**
     * The extension of the filename
     */
    extension: {
      type: String,
      required: true
    },
    filesListWidth: {
      type: Number,
      required: true
    },
    nodes: {
      type: Array,
      required: true
    },
    source: {
      type: Object,
      required: true
    },
    gridMode: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    const {
      currentView
    } = useNavigation();
    const {
      directory
    } = useRouteParameters();
    const renamingStore = useRenamingStore();
    const defaultFileAction = inject('defaultFileAction');
    return {
      currentView,
      defaultFileAction,
      directory,
      renamingStore
    };
  },
  computed: {
    isRenaming() {
      return this.renamingStore.renamingNode === this.source;
    },
    isRenamingSmallScreen() {
      return this.isRenaming && this.filesListWidth < 512;
    },
    newName: {
      get() {
        return this.renamingStore.newName;
      },
      set(newName) {
        this.renamingStore.newName = newName;
      }
    },
    renameLabel() {
      const matchLabel = {
        [FileType.File]: t('files', 'Filename'),
        [FileType.Folder]: t('files', 'Folder name')
      };
      return matchLabel[this.source.type];
    },
    linkTo() {
      if (this.source.status === NodeStatus.FAILED) {
        return {
          is: 'span',
          params: {
            title: t('files', 'This node is unavailable')
          }
        };
      }
      if (this.defaultFileAction && this.currentView) {
        const displayName = this.defaultFileAction.displayName([this.source], this.currentView);
        return {
          is: 'button',
          params: {
            'aria-label': displayName,
            title: displayName,
            tabindex: '0'
          }
        };
      }
      // nothing interactive here, there is no default action
      // so if not even the download action works we only can show the list entry
      return {
        is: 'span'
      };
    }
  },
  watch: {
    /**
     * If renaming starts, select the filename
     * in the input, without the extension.
     * @param renaming
     */
    isRenaming: {
      immediate: true,
      handler(renaming) {
        if (renaming) {
          this.startRenaming();
        }
      }
    },
    newName() {
      // Check validity of the new name
      const newName = this.newName.trim?.() || '';
      const input = this.$refs.renameInput?.$el.querySelector('input');
      if (!input) {
        return;
      }
      let validity = getFilenameValidity(newName);
      // Checking if already exists
      if (validity === '' && this.checkIfNodeExists(newName)) {
        validity = t('files', 'Another entry with the same name already exists.');
      }
      this.$nextTick(() => {
        if (this.isRenaming) {
          input.setCustomValidity(validity);
          input.reportValidity();
        }
      });
    }
  },
  methods: {
    checkIfNodeExists(name) {
      return this.nodes.find(node => node.basename === name && node !== this.source);
    },
    startRenaming() {
      this.$nextTick(() => {
        // Using split to get the true string length
        const input = this.$refs.renameInput?.$el.querySelector('input');
        if (!input) {
          logger.error('Could not find the rename input');
          return;
        }
        input.focus();
        const length = this.source.basename.length - (this.source.extension ?? '').length;
        input.setSelectionRange(0, length);
        // Trigger a keyup event to update the input validity
        input.dispatchEvent(new Event('keyup'));
      });
    },
    stopRenaming() {
      if (!this.isRenaming) {
        return;
      }
      // Reset the renaming store
      this.renamingStore.$reset();
    },
    // Rename and move the file
    async onRename() {
      const newName = this.newName.trim?.() || '';
      const form = this.$refs.renameForm;
      if (!form.checkValidity()) {
        showError(t('files', 'Invalid filename.') + ' ' + getFilenameValidity(newName));
        return;
      }
      const oldName = this.source.basename;
      const oldEncodedSource = this.source.encodedSource;
      if (oldName === newName) {
        this.stopRenaming();
        return;
      }
      // Set loading state
      this.$set(this.source, 'status', NodeStatus.LOADING);
      // Update node
      this.source.rename(newName);
      logger.debug('Moving file to', {
        destination: this.source.encodedSource,
        oldEncodedSource
      });
      try {
        await axios({
          method: 'MOVE',
          url: oldEncodedSource,
          headers: {
            Destination: this.source.encodedSource,
            Overwrite: 'F'
          }
        });
        // Success 
        emit('files:node:updated', this.source);
        emit('files:node:renamed', this.source);
        emit('files:node:moved', {
          node: this.source,
          oldSource: `${dirname(this.source.source)}/${oldName}`
        });
        showSuccess(t('files', 'Renamed "{oldName}" to "{newName}"', {
          oldName,
          newName
        }));
        // Reset the renaming store
        this.stopRenaming();
        this.$nextTick(() => {
          const nameContainter = this.$refs.basename;
          nameContainter?.focus();
        });
      } catch (error) {
        logger.error('Error while renaming file', {
          error
        });
        // Rename back as it failed
        this.source.rename(oldName);
        // And ensure we reset to the renaming state
        this.startRenaming();
        if (isAxiosError(error)) {
          // TODO: 409 means current folder does not exist, redirect ?
          if (error?.response?.status === 404) {
            showError(t('files', 'Could not rename "{oldName}", it does not exist any more', {
              oldName
            }));
            return;
          } else if (error?.response?.status === 412) {
            showError(t('files', 'The name "{newName}" is already used in the folder "{dir}". Please choose a different name.', {
              newName,
              dir: this.directory
            }));
            return;
          }
        }
        // Unknown error
        showError(t('files', 'Could not rename "{oldName}"', {
          oldName
        }));
      } finally {
        this.$set(this.source, 'status', undefined);
      }
    },
    t
  }
});