import { NodeStatus, getFileActions } from '@nextcloud/files';
import { showError, showSuccess } from '@nextcloud/dialogs';
import { translate } from '@nextcloud/l10n';
import { defineComponent } from 'vue';
import NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton.js';
import NcActions from '@nextcloud/vue/dist/Components/NcActions.js';
import NcIconSvgWrapper from '@nextcloud/vue/dist/Components/NcIconSvgWrapper.js';
import NcLoadingIcon from '@nextcloud/vue/dist/Components/NcLoadingIcon.js';
import { useRouteParameters } from "../composables/useRouteParameters.ts";
import { useActionsMenuStore } from "../store/actionsmenu.ts";
import { useFilesStore } from "../store/files.ts";
import { useSelectionStore } from "../store/selection.ts";
import filesListWidthMixin from "../mixins/filesListWidth.ts";
import logger from "../logger.ts";
// The registered actions list
const actions = getFileActions();
export default defineComponent({
  name: 'FilesListTableHeaderActions',
  components: {
    NcActions,
    NcActionButton,
    NcIconSvgWrapper,
    NcLoadingIcon
  },
  mixins: [filesListWidthMixin],
  props: {
    currentView: {
      type: Object,
      required: true
    },
    selectedNodes: {
      type: Array,
      default: () => []
    }
  },
  setup() {
    const actionsMenuStore = useActionsMenuStore();
    const filesStore = useFilesStore();
    const selectionStore = useSelectionStore();
    const {
      directory
    } = useRouteParameters();
    return {
      directory,
      actionsMenuStore,
      filesStore,
      selectionStore
    };
  },
  data() {
    return {
      loading: null
    };
  },
  computed: {
    enabledActions() {
      return actions.filter(action => action.execBatch).filter(action => !action.enabled || action.enabled(this.nodes, this.currentView)).sort((a, b) => (a.order || 0) - (b.order || 0));
    },
    nodes() {
      return this.selectedNodes.map(source => this.getNode(source)).filter(Boolean);
    },
    areSomeNodesLoading() {
      return this.nodes.some(node => node.status === NodeStatus.LOADING);
    },
    openedMenu: {
      get() {
        return this.actionsMenuStore.opened === 'global';
      },
      set(opened) {
        this.actionsMenuStore.opened = opened ? 'global' : null;
      }
    },
    inlineActions() {
      if (this.filesListWidth < 512) {
        return 0;
      }
      if (this.filesListWidth < 768) {
        return 1;
      }
      if (this.filesListWidth < 1024) {
        return 2;
      }
      return 3;
    }
  },
  methods: {
    /**
     * Get a cached note from the store
     *
     * @param source The source of the node to get
     */
    getNode(source) {
      return this.filesStore.getNode(source);
    },
    async onActionClick(action) {
      const displayName = action.displayName(this.nodes, this.currentView);
      const selectionSources = this.selectedNodes;
      try {
        // Set loading markers
        this.loading = action.id;
        this.nodes.forEach(node => {
          this.$set(node, 'status', NodeStatus.LOADING);
        });
        // Dispatch action execution
        const results = await action.execBatch(this.nodes, this.currentView, this.directory);
        // Check if all actions returned null
        if (!results.some(result => result !== null)) {
          // If the actions returned null, we stay silent
          this.selectionStore.reset();
          return;
        }
        // Handle potential failures
        if (results.some(result => result === false)) {
          // Remove the failed ids from the selection
          const failedSources = selectionSources.filter((source, index) => results[index] === false);
          this.selectionStore.set(failedSources);
          if (results.some(result => result === null)) {
            // If some actions returned null, we assume that the dev
            // is handling the error messages and we stay silent
            return;
          }
          showError(this.t('files', '"{displayName}" failed on some elements ', {
            displayName
          }));
          return;
        }
        // Show success message and clear selection
        showSuccess(this.t('files', '"{displayName}" batch action executed successfully', {
          displayName
        }));
        this.selectionStore.reset();
      } catch (e) {
        logger.error('Error while executing action', {
          action,
          e
        });
        showError(this.t('files', '"{displayName}" action failed', {
          displayName
        }));
      } finally {
        // Remove loading markers
        this.loading = null;
        this.nodes.forEach(node => {
          this.$set(node, 'status', undefined);
        });
      }
    },
    t: translate
  }
});